import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class GooglSheetService {
  //private googleScriptURL = 'https://script.google.com/macros/s/AKfycbxQpWDIPqT1LpJcofqDL1hxzUwjP1zV-lNMmNY4H2sOJoI4oxJRiVy8vMianAy5D35X/exec';
  private  googleScriptURL='https://script.google.com/macros/s/AKfycby6zSSdD-4bC1Bzojrt-BhSuNUCDfB1pxaqVqpVb0oQ1g-X7elHJYTCjWDrr49GnNtZ6A/exec'
  //questianrie 1://'https://script.google.com/macros/s/AKfycbz02gUhZQC8hLQ6H3lp1-C1_YpoqIlla54hM-cYGXALtuibBmsSE8AVgoHM_RGyB9oM/exec';
  
  constructor(private http: HttpClient) {}

  // postData2(data: any): Observable<any> {
  //   const headers = new HttpHeaders({ 'Content-Type': 'application/json' });
  //   return this.http.post(this.googleScriptURL, data, { headers });
  // }
  postData(data: any): Promise<void> {
    return fetch(this.googleScriptURL, {
      method: 'POST',
      mode: 'no-cors', // Bypass CORS issues
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    })
      .then(() => {
        console.log('Data sent successfully');
      })
      .catch((error) => {
        console.error('Error sending data:', error);
        throw error;
      });
  }  
}
